header {
    @apply bg-white bg-opacity-95 w-auto laptop:h-16 shadow-md flex flex-row border-indigo-400 border-t-0
    iphone:h-10;
}

.logo-header {
    @apply laptop:w-16 laptop:h-16
    iphone:w-10 iphone:h-10;
}

.title-header {
    @apply laptop:text-4xl text-black text-opacity-80 font-sacramento laptop:mt-5
    iphone:text-2xl iphone:mt-2;
}


.login-container {
    @apply flex min-h-screen bg-gradient-to-r from-sky-blue via-blue-300 to-sky-blue justify-center self-center items-center font-thasadith text-center
    iphone:text-sm
    ipad:text-base
    laptop:text-xl;
}
.main-container {
    @apply  laptop:w-full h-full justify-center self-center items-center p-10
}

.section-login {
    @apply flex flex-col h-auto  justify-center self-center items-center
    iphone:w-5/6
    laptop:flex-row;
}

.form {
   @apply flex flex-col gap-4  justify-center self-center items-center bg-white rounded-md border-gray-300 border-2 shadow-2xl
   iphone:w-64 iphone:mt-0 iphone:mb-5 iphone:h-auto iphone:p-6 iphone:pb-14 iphone:pt-14
   ipad:w-auto ipad:h-auto ipad:p-20
   laptop:w-auto laptop:p-28 laptop:pl-6 laptop:pr-6 laptop:m-5 laptop:mb-6 
}


.h2 {
    @apply uppercase text-gray-800  font-thasadith font-black mb-6 
    iphone:text-sm
    ipad:text-base
    laptop:text-3xl;
}

.login-img {
    @apply lg:w-5/6 lg:h-5/6 mb-4 rounded-2xl
    iphone:w-auto iphone:h-auto;
}


.login-images {
    @apply inline-flex justify-center self-center items-center
    iphone:w-auto iphone:h-auto iphone:mt-5 iphone:mb-0;
}

.logo-container {
    @apply flex-row w-auto h-auto p-6 font-thasadith justify-center items-center laptop:text-2xl 
    iphone:text-sm;
}

.login-input {
    @apply font-bold text-black focus:outline-none focus:ring focus:border-gray-700 p-2 rounded font-thasadith  justify-center self-center items-center
    iphone:text-sm iphone:w-4/6 iphone:m-0 iphone:border-gray-600 iphone:border-2
    ipad:text-lg
    laptop:mb-5 laptop:w-56 laptop:h-auto laptop:text-lg;
}

.error-login {
    @apply  rounded p-3 text-center text-white font-thasadith bg-red-700 select-none
    iphone:w-full iphone:p-0 iphone:-mt-5
    ipad:p-2
}

.login-button {
    @apply mt-6  transition rounded border border-blue-500 duration-300 ease-in-out text-xl uppercase bg-dark-blue bg-opacity-80 hover:bg-opacity-95 py-2 px-4 text-gray-100
    iphone:w-3/6 iphone:text-sm
    ipad:w-3/6 ipad:h-auto ipad:text-sm ipad:px-0
    laptop:w-auto laptop:h-auto laptop:text-lg;
}

.login-button img {
    @apply w-auto h-auto
    iphone:w-3/6 iphone:m-auto
    ipad:w-2/6 ipad:m-auto
}

.form-footer {
    @apply inline-flex flex-row justify-center self-center items-center bg-gradient-to-r from-sky-blue via-blue-300 to-sky-blue rounded-2xl p-2 -ml-10 -mr-10
    iphone:w-auto iphone:h-auto iphone:mt-5 iphone:mb-0;
}

.footer-text-login {
    @apply inline-flex text-center text-black font-thasadith laptop:rounded-md laptop:gap-4  justify-center self-center items-center
    iphone:text-sm iphone:bg-transparent iphone:h-auto iphone:p-0 iphone:mb-0 iphone:border-0 iphone:shadow-none
    ipad:text-base;
}

