.section-container { 
    @apply flex flex-col gap-8 bg-gradient-to-r from-sky-blue via-blue-300 to-sky-blue
}

.user-container { 
    @apply flex flex-col gap-8 ipad:w-9/12 laptop:w-6/12 PC:w-4/12 ml-auto mr-auto iphone:p-2 mt-auto pt-4 rounded-3xl  shadow-md
}

.header-user {
    @apply flex flex-row justify-between items-center bg-white w-full h-full border-b-2 border-blue-200 sticky top-0 z-10 shadow-md
}
.header-user__logo{
    @apply flex flex-row justify-center items-center
}

.logout-container {
    @apply flex flex-row gap-4 w-auto h-auto iphone:mr-0 ipad:mr-5
}


.logo-header {
    @apply laptop:w-16 laptop:h-16 
    iphone:w-10 iphone:h-10
    ipad:w-12 ipad:h-12;
}

.title-header {
    @apply laptop:text-4xl text-black text-opacity-80 font-sacramento laptop:mt-5 laptop:mr-96
    iphone:hidden ipad:flex
    ipad:text-3xl ipad:mt-4;
}


.user-title {
    @apply m-auto gap-4 font-thasadith font-black text-gray-700 laptop:text-xl iphone:text-xs ipad:text-base
}


