.toast-notification-container {
    @apply flex flex-row font-thasadith font-black;
}

toast-icon-notification-container {
    @apply flex flex-row items-center mt-16;
}

.toast-notification-text-container  {
    @apply flex flex-row flex-wrap items-center m-auto;
}

.posts-container-general{
    @apply flex flex-col gap-8 border border-blue-300 shadow-2xl ipad:m-16 mb-10 bg-white hover:shadow-xl transform-gpu hover:translate-y-1 rounded-md
    iphone:w-full
    ipad:w-auto;
}

.username-post-container {
    @apply flex flex-row items-center gap-2 text-2xl p-4 border-b border-gray-300 bg-gray-700 text-white shadow-md rounded-md;
}

.title-post-container {
    @apply flex flex-col gap-2 ipad:text-2xl pl-4 pr-4 text-gray-700 font-black font-thasadith text-center;
}

.title-post-container h1 {
    @apply uppercase
}

.img-post-container {
    @apply flex flex-col gap-2 text-center border-t-2 border-gray-300 border-b-2 shadow-lg;
}

.img-post-container img {
    @apply object-scale-down object-center w-auto h-auto max-h-96 ;
}

.description-post-container {
    @apply flex flex-col gap-2 text-gray-700 font-thasadith text-center font-semibold break-words m-4;
}

.description-post-container p {
    @apply border-blue-700;
}


.footer-post-container {
    @apply flex flex-row gap-2 text-gray-700 font-thasadith text-center font-semibold;
}

.likes-container {
    @apply flex flex-col rounded-md w-full justify-end mb-auto;
}

.comments-container {
    @apply flex flex-col rounded-md w-full justify-end mb-auto overflow-hidden;
}

.fav-container {
    @apply flex flex-col rounded-md justify-end iphone:mt-7 ipad:mt-8 laptop:mt-9;
}

.lenght-container {
    @apply text-black iphone:text-xl ipad:text-2xl laptop:text-3xl font-thasadith m-0;
}
.title-add-comment {
    @apply font-thasadith font-semibold text-center;
}
.container-add-comment {
    @apply flex flex-col gap-2 text-gray-700 font-thasadith text-center font-black;
}

.form-add-comment {
    @apply gap-2 mb-3 text-gray-700 font-thasadith text-center font-black;
}

.input-add-comment {
    @apply w-11/12 p-2 border-2 rounded-md focus:outline-none focus:ring m-auto text-gray-700 font-black;
}

.post-comment {
    @apply flex flex-col gap-2 text-gray-700 font-thasadith text-center font-semibold break-words m-4 border border-gray-300 shadow rounded-md;
}

.post-comment p{
    @apply max-w-prose;
}

.post-comment h1 {
    @apply uppercase text-blue-500;
}

.button-add-comment {
    @apply transition rounded border text-white border-blue-500 duration-300 ease-in-out font-thasadith font-black py-1 px-2 bg-blue-500 hover:bg-blue-700 mt-5;
}