.container-notfound {
    @apply flex flex-col justify-center items-center w-screen h-screen bg-gradient-to-r from-sky-blue via-blue-300 to-sky-blue
}

.title-notfound {
    @apply text-center font-thin font-thasadith p-10 text-black
    iphone:text-6xl
    ipad:text-8xl
    laptop:text-9xl
}

.img-notfound{
    @apply 
    iphone:w-40 iphone:h-auto iphone:justify-center iphone:self-center
    ipad:w-72 ipad:h-auto
    laptop:w-96 h-auto;
}

.notfound {
    @apply flex laptop:flex-row bg-gray-200 p-2 shadow-xl border-2 border-gray-300 rounded-lg m-10 mt-2
    iphone:flex-col
    laptop:p-5  laptop:m-12
}

.text-notfound-container {
    @apply flex text-center justify-center text-5xl font-bold font-thasadith flex-col text-dark-blue;
}

.text-notfound {
    @apply text-6xl font-bold font-thasadith p-5
    iphone:text-xl
    ipad:text-2xl
    laptop:text-3xl
}

.subtext-notfound {
    @apply text-2xl font-normal font-thasadith p-8
    iphone:text-base
    ipad:text-lg
    laptop:text-2xl
}

.button-notfound {
    @apply text-2xl font-bold font-thasadith p-8 border-2 border-gray-300 rounded-lg bg-red-50 hover:bg-red-300
    iphone:text-base
    ipad:text-lg
    laptop:text-3xl
}