
.section-container { 
    @apply flex flex-col gap-8 bg-gradient-to-r from-sky-blue via-blue-300 to-sky-blue
}

.admin-container { 
    @apply flex flex-col gap-8 ipad:w-9/12 laptop:w-6/12 PC:w-4/12 ml-auto mr-auto iphone:p-2 mt-auto pt-4 rounded-3xl  shadow-md
}

.header-admin {
    @apply flex flex-row justify-between items-center bg-white w-full h-full border-b-2 border-blue-200 sticky top-0 z-10 shadow-md
}
.header-admin__logo{
    @apply flex flex-row justify-center items-center
}

.logout-container {
    @apply flex flex-row gap-4 w-auto h-auto iphone:mr-0 ipad:mr-5
}


.logo-header {
    @apply laptop:w-16 laptop:h-16 
    iphone:w-10 iphone:h-10
    ipad:w-12 ipad:h-12;
}

.title-header {
    @apply laptop:text-4xl text-black text-opacity-80 font-sacramento laptop:mt-5 laptop:mr-96
    iphone:hidden ipad:flex
    ipad:text-3xl ipad:mt-4;
}


.user-title {
    @apply m-auto gap-4 font-thasadith font-black text-gray-700 laptop:text-xl iphone:text-xs ipad:text-base
}



.form-createPost {
    @apply flex flex-col ml-10 mr-10 gap-4 items-center justify-center border-gray-300 border rounded-lg shadow-2xl bg-white w-auto p-6  hover:shadow-xl transform-gpu hover:translate-y-1
    ipad:p-4 ipad:ml-28 ipad:mr-28 ipad:border-2
    laptop:ml-20 laptop:mr-20 
    PC:ml-36 PC:mr-36
}

.createPost-title {
    @apply text-center uppercase text-gray-800 font-thasadith font-black laptop:text-4xl mb-4;
}
.input-container {
    @apply flex flex-row gap-4 justify-center items-center border-b-2 border-blue-500 w-full;
}

.description-input {
    @apply font-black w-full text-black focus:outline-none focus:ring focus:border-gray-700 p-2 rounded font-thasadith bg-transparent placeholder-gray-500 m-2 laptop:text-lg
}
.title-section {
    @apply flex flex-row text-gray-800 font-thasadith font-bold laptop:text-lg;
}

.title-input {
    @apply font-black w-full text-black focus:outline-none focus:ring focus:border-gray-700 p-2 rounded font-thasadith bg-transparent placeholder-gray-500 m-2 laptop:text-lg
}

.container-createPost_button {
    @apply flex flex-row items-center justify-end w-auto;
}

.button-createPost {
    @apply flex mt-2 w-auto justify-end transition rounded-xl border border-gray-300 duration-300 ease-in-out laptop:text-xl font-thasadith uppercase shadow-2xl
    bg-blue-600 hover:bg-blue-700 py-2 px-4 text-gray-100
}

.toast-notification-container {
    @apply flex flex-row font-thasadith font-black;
}

toast-icon-notification-container {
    @apply flex flex-row items-center mt-16;
}

.toast-notification-text-container  {
    @apply flex flex-row flex-wrap items-center m-auto;
}
